@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Liu+Jian+Mao+Cao&family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Beth+Ellen&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap);
:root{
  --black:#09090c;
  --grey:#a4b2bc;
  --white:#fff;
  --background:rgba(137,171,245,0.37);
  /* --background:#ffd1dc; */
}

html{
  overflow: hidden;
}

*,*::before,*::after{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(137,171,245,0.37);
  background-color: var(--background);
  font-family: 'Poppins',sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#timeline{display:-webkit-flex;display:flex;width:100%;position:absolute;background-color:#031625}#timeline:hover .tl-item{width:23.3333%}.tl-item{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);position:relative;width:25%;height:100vh;min-height:600px;color:#fff;overflow:hidden;transition:width 0.5s ease}.tl-item:before,.tl-item:after{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);content:'';position:absolute;left:0;top:0;width:100%;height:100%}.tl-item:after{background:rgba(36,48,94,0.85);opacity:1;transition:opacity 0.5s ease}.tl-item:before{background:linear-gradient(to bottom, rgba(0,0,0,0) 0%, #000 75%);z-index:1;opacity:0;-webkit-transform:translate3d(0, 0, 0) translateY(50%);transform:translate3d(0, 0, 0) translateY(50%);transition:opacity 0.5s ease, -webkit-transform 0.5s ease;transition:opacity 0.5s ease, transform 0.5s ease;transition:opacity 0.5s ease, transform 0.5s ease, -webkit-transform 0.5s ease}.tl-item:hover{width:30% !important}.tl-item:hover:after{opacity:0}.tl-item:hover:before{opacity:1;-webkit-transform:translate3d(0, 0, 0) translateY(0);transform:translate3d(0, 0, 0) translateY(0);transition:opacity 1s ease, -webkit-transform 1s ease 0.25s;transition:opacity 1s ease, transform 1s ease 0.25s;transition:opacity 1s ease, transform 1s ease 0.25s, -webkit-transform 1s ease 0.25s}.tl-item:hover .tl-content{opacity:1;-webkit-transform:translateY(0);transform:translateY(0);transition:all 0.75s ease 0.5s}.tl-item:hover .tl-bg{-webkit-filter:grayscale(0);filter:grayscale(0)}.tl-content{-webkit-transform:translate3d(0, 0, 0) translateY(25px);transform:translate3d(0, 0, 0) translateY(25px);position:relative;z-index:1;text-align:center;margin:0 1.618em;top:55%;opacity:0}.tl-content h1{font-family:'Amatic SC', cursive;text-transform:uppercase;color:#1779cf;font-size:2rem;font-weight:normal}.tl-year{position:absolute;top:50%;left:50%;-webkit-transform:translateX(-50%) translateY(-50%);transform:translateX(-50%) translateY(-50%);z-index:1;border-top:1px solid #fff;border-bottom:1px solid #fff}.tl-year p{font-family:'Amatic SC', cursive;font-size:2.5rem;line-height:0}.tl-content p{font-family:'Nanum Pen Script', cursive;font-size:1.5rem}.tl-bg{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);position:absolute;width:100%;height:100%;top:0;left:0;background-size:cover;background-position:center center;transition:-webkit-filter 0.5s ease;transition:filter 0.5s ease;transition:filter 0.5s ease, -webkit-filter 0.5s ease;-webkit-filter:grayscale(100%);filter:grayscale(100%)}

